"use client"
// src/components/HourlyForecast.tsx
import { useState } from "react"

export default function HourlyForecast({ data, selectedDay, onSelectDay }) {
  const [showDayDropdown, setShowDayDropdown] = useState(false)
  const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]

  return (
    <div className="bg-slate-800 border border-slate-700 rounded-2xl p-6 h-full">
      <div className="flex justify-between items-center mb-6">
        <h3 className="text-xl font-bold text-slate-50">Hourly forecast</h3>

        <div className="relative">
          <button
            onClick={() => setShowDayDropdown(!showDayDropdown)}
            className="flex items-center gap-2 px-3 py-1 rounded-lg bg-slate-700 hover:bg-slate-600 text-slate-50 text-sm border border-slate-600 transition-colors"
          >
            {selectedDay}
            <span>‚ñº</span>
          </button>

          {showDayDropdown && (
            <div className="absolute right-0 mt-2 w-40 bg-slate-700 border border-slate-600 rounded-lg shadow-xl z-50">
              {days.map((day) => (
                <button
                  key={day}
                  onClick={() => {
                    onSelectDay(day)
                    setShowDayDropdown(false)
                  }}
                  className={`block w-full text-left px-4 py-2 hover:bg-slate-600 transition-colors border-b border-slate-600 last:border-b-0 ${
                    day === selectedDay ? "bg-slate-600 text-white" : "text-slate-300"
                  }`}
                >
                  {day}
                </button>
              ))}
            </div>
          )}
        </div>
      </div>

      <div className="space-y-3 max-h-96 overflow-y-auto">
        {data.hourly.map((hour, index) => (
          <div
            key={index}
            className="flex items-center justify-between p-3 bg-slate-700 rounded-lg hover:bg-slate-600 transition-colors"
          >
            <div className="flex items-center gap-3 flex-1">
              <span className="text-2xl">{hour.icon}</span>
              <span className="text-slate-300 font-semibold">{hour.time}</span>
            </div>
            <span className="text-slate-50 font-semibold">{hour.temp}¬∞</span>
          </div>
        ))}
      </div>
    </div>
  )
}
