"use client"
// src/components/Header.tsx
import { useState } from "react"

export default function Header({ unit, onUnitChange }) {
  const [showUnits, setShowUnits] = useState(false)

  return (
    <header className="border-b border-slate-700 bg-slate-950">
      <div className="container mx-auto px-4 py-4 flex justify-between items-center max-w-7xl">
        <div className="flex items-center gap-2">
          <span className="text-3xl">‚òÄÔ∏è</span>
          <h1 className="text-2xl font-bold text-slate-50">Weather Now</h1>
        </div>

        <div className="relative">
          <button
            onClick={() => setShowUnits(!showUnits)}
            className="flex items-center gap-2 px-4 py-2 rounded-lg bg-slate-800 hover:bg-slate-700 text-slate-50 border border-slate-600 transition-colors"
          >
            <span>‚öôÔ∏è</span>
            <span>Units</span>
            <span>‚ñº</span>
          </button>

          {showUnits && (
            <div className="absolute right-0 mt-2 w-64 bg-slate-800 border border-slate-600 rounded-lg shadow-xl z-50">
              <div className="p-4">
                <div className="text-sm text-slate-400 mb-4">
                  <p className="font-semibold text-slate-300 mb-2">Temperature</p>
                  <button
                    onClick={() => {
                      onUnitChange("metric")
                      setShowUnits(false)
                    }}
                    className={`block w-full text-left px-3 py-2 rounded mb-2 transition-colors ${
                      unit === "metric" ? "bg-indigo-600 text-white" : "hover:bg-slate-700 text-slate-300"
                    }`}
                  >
                    Celsius (¬∞C) {unit === "metric" && "‚úì"}
                  </button>
                  <button
                    onClick={() => {
                      onUnitChange("imperial")
                      setShowUnits(false)
                    }}
                    className={`block w-full text-left px-3 py-2 rounded transition-colors ${
                      unit === "imperial" ? "bg-indigo-600 text-white" : "hover:bg-slate-700 text-slate-300"
                    }`}
                  >
                    Fahrenheit (¬∞F) {unit === "imperial" && "‚úì"}
                  </button>
                </div>

                <div className="text-sm text-slate-400 mb-4 border-t border-slate-700 pt-4">
                  <p className="font-semibold text-slate-300 mb-2">Wind Speed</p>
                  <p className="text-xs text-slate-500 mb-2">km/h {unit === "metric" && "‚úì"}</p>
                  <p className="text-xs text-slate-500">mph {unit === "imperial" && "‚úì"}</p>
                </div>

                <div className="text-sm text-slate-400 border-t border-slate-700 pt-4">
                  <p className="font-semibold text-slate-300 mb-2">Precipitation</p>
                  <p className="text-xs text-slate-500 mb-2">Millimeters (mm) {unit === "metric" && "‚úì"}</p>
                  <p className="text-xs text-slate-500">Inches (in) {unit === "imperial" && "‚úì"}</p>
                </div>

                <button
                  onClick={() => {
                    onUnitChange(unit === "metric" ? "imperial" : "metric")
                    setShowUnits(false)
                  }}
                  className="w-full mt-4 px-3 py-2 rounded border border-slate-600 text-slate-300 hover:bg-slate-700 transition-colors text-sm"
                >
                  Switch to {unit === "metric" ? "Imperial" : "Metric"}
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </header>
  )
}
