// src/components/WeatherContent.tsx
'use client'
import ErrorState from "./states/ErrorState"
import LoadingState from "./states/LoadingState"
import WeatherCard from "./WeatherCard"
import MetricsGrid from "./MetricsGrid"
import DailyForecast from "./DailyForecast"
import HourlyForecast from "./HourlyForecast"
import NoResultsState from "./states/NoResultsState"

export default function WeatherContent({ weatherData, loading, error, onRetry, selectedDay, onSelectDay }) {
  if (error) {
    return <ErrorState onRetry={onRetry} />
  }

  if (loading) {
    return <LoadingState />
  }

  if (!weatherData) {
    return <NoResultsState />
  }

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
      <div className="lg:col-span-2 space-y-6">
        <WeatherCard data={weatherData} />
        <MetricsGrid data={weatherData} />
        <DailyForecast data={weatherData} />
      </div>

      <div className="lg:col-span-1">
        <HourlyForecast data={weatherData} selectedDay={selectedDay} onSelectDay={onSelectDay} />
      </div>
    </div>
  )
}
